.swiper-container {
  width: 100%;
  height: 100%;
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    min-height: 200px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: flex-start;
  }
}